.Button {
   background-color: transparent;
   border: 1px solid;
   color: white;
   outline: none;
   cursor: pointer;
   font: inherit;
   padding: 10px;
   margin: 0 10px;
   font-weight: bold;
}


.Button:disabled{
   color: #ccc;
   border-color: #ccc;
   cursor: not-allowed;
}

.Success {
   color: #5C9210;
   border-color:#5C9210;
}
.Danger {
   color: #944317;
   border-color:#944317;
}