.NavigationItems{
   padding: 0;
   margin: 0;
   list-style: none;
   display: flex;
   flex-flow: column;
   align-items: center;
   height: 100%;
}
.NavigationItems:before{
   content:"";
   background-color: red;
   height: 2px;
   width: 100%;
}

@media (min-width: 500px){
   .NavigationItems{
      width: auto;
      flex-flow: row;
   }
   .NavigationItems:before{
      display:none;
   }
}