.BuildControls {
    width: 100%;
    /* background-color: #CF8F2E; */
    background-color: #e67e22;
    color: #1a252f;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}


.OrderButton {
    background-color: #f1c40f;
    border-radius: 5px;
    outline: none;
    cursor: pointer;
    border: 1px solid #d35400;
    color: #d35400;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    box-shadow: -2px 2px 2px #d35400;
    margin: 30px 0;
    transition: all .2s;
}

.OrderButton:hover, .OrderButton:active {
    background-color: #A0DB41;
    border: 1px solid #7e3200;
    color: #7e3200;
    box-shadow: -4px 4px 2px #d35400;
    transform: translate(2px, -2px);
}

.OrderButton:disabled {
    background-color: #C7C6C6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.OrderButton:not(:disabled) {
    animation: enable 0.3s linear;
}

@keyframes enable {
    0% {
        transform: scale(1);
    }
    60% {
        transform: scale(1.1);
    }
    100% {
        transform: scale(1);
    }
}