.Modal {
   position: fixed;
   z-index: 500;
   background-color: #ecf0f1;
   width: 70%;
   border: 1px solid #ccc;
   box-shadow: 10px 10px 30px rgba(0,0,0,.2);
   padding: 16px;
   left: 15%;
   top: 15%;
   box-sizing: border-box;
   transition: all 0.3s ease-out;
   border-radius: 5px;
   
   display:flex;
   flex-direction: column;
   align-items: center;
}
.Modal h3{
   color: #d35400;
   text-align: center;
   align-self: stretch;
}

.Modal h3:after{
   margin-top: 15px;
   display: block;
   content: " ";
   width: 100%;
   height: 1px;
   background-color: #d35400;
}

.Modal p{
   text-align: center;
   color: #2c3e50;
}

.Modal ul{
   list-style-type: circle;
   color: #d35400;
}
.Modal div{
   align-self: flex-end;
}

@media (min-width: 600px) {
   .Modal {
       width: 500px;
       left: calc(50% - 250px);
   }
}