.NavigationItem {
   margin: 10px 0;
   box-sizing: border-box;
   display: block;
   width: 100%;
}

.NavigationItem a {
   /* color: #8f5c2c; */
   color: #e67e22;
   text-decoration: none;
   width: 100%;
   box-sizing: border-box;
   display: block;
}

.NavigationItem a:active,
.NavigationItem a:hover,
.NavigationItem a.active {
   color: #3498db;
}

@media (min-width: 500px) {
   .NavigationItem {
      margin: 0;
      display: flex;
      height: 100%;
      width: auto;
      align-items: center;
   }

   .NavigationItem a {
      color: white;
      height: 100%;
      padding: 16px 10px;
      border-bottom: 4px solid transparent;
   }

   .NavigationItem a:active,
   .NavigationItem a:hover,
   .NavigationItem a.active {
      background-color: #ffbb8f;
      border-bottom: 4px solid #40A4C8;
      color: #40A4C8;
   }
}